'use strict';
import { split, isString, isArray, find, eq, isNumber } from "lodash/fp";
/**
 * @desc
 * 
 * 1, 2, 3QQ, 4, 5, 6, 7, 8, 9, 10
 *
 * @param
 * {'1,2'} 
 * {[1,2]} 
 * {'all'} 
 */
var fn = function (param) {
    var list = [
        { dataIndex: 1, title: '' },
        { dataIndex: 2, title: '' },
        { dataIndex: 3, title: 'QQ' },
        { dataIndex: 4, title: '' },
        { dataIndex: 5, title: '' },
        { dataIndex: 6, title: '' },
        { dataIndex: 7, title: '' },
        { dataIndex: 8, title: '' },
        { dataIndex: 9, title: '' },
        { dataIndex: 10, title: '' }
    ];
    var paramOk = [];
    var result = [];
    if (eq(param, 'all')) {
        return list;
    }
    else if (isString(param) || isNumber(param)) {
        paramOk = split(',', String(param)).map(function (v) { return +v; });
    }
    else if (isArray(param)) {
        paramOk = param.map(function (v) { return +v; });
    }
    paramOk.map(function (v) {
        var row = find({ dataIndex: v }, list) || { title: '' };
        result.push(row.title);
    });
    return String(result);
};
export default fn;
